/**
 * 计算两种颜色的对比度，一般指前景色和背景色
 * https://www.w3.org/TR/WCAG20-TECHS/G17.html#G17-tests 第3点
 */
/**
 * Applies the correct theme color style to the specified property.
 * $property is typically color or background-color, but can be any CSS property that accepts color values.
 * $style should be one of the map keys in $theme-property-values (_variables.scss).
 */
/**
 * 计算两种颜色的对比度，一般指前景色和背景色
 * https://www.w3.org/TR/WCAG20-TECHS/G17.html#G17-tests 第3点
 */
@keyframes ripple-radius-in {
  from {
    transform: translate(var(--ripple-translate-start, 0)) scale(1);
    animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
  to {
    transform: translate(var(--ripple-translate-end, 0)) scale(var(--ripple-scale, 1));
  }
}

@keyframes ripple-radius-out {
  from {
    transform: translate(var(--ripple-translate-end, 0)) scale(var(--ripple-scale, 1));
  }
  to {
    transform: translate(var(--ripple-translate-start, 0)) scale(1);
    animation-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
}

@keyframes ripple-opacity-in {
  from {
    opacity: 0;
    animation-timing-function: linear;
  }
  to {
    opacity: 1;
  }
}

@keyframes ripple-opacity-out {
  from {
    opacity: 1;
    animation-timing-function: linear;
  }
  to {
    opacity: 0;
  }
}

/**
* 关于 font weight 的设置，跟字体粗细值有关
* 1. 在 Safari 和 IOS 系统下，默认字体粗细分为
    100-300   400-500  600-700  800-900  四种，对应为
    lighter 比从父元素继承来的值更细(处在字体可行的粗细值范围内)。
    normal 正常粗细，与400等值。
    bold 加粗， 与700等值
    bolder 比从父元素继承来的值更粗 (处在字体可行的粗细值范围内)。
 如果设为 -apple-system 或 "PingFang SC"时，就会有更多的字体粗细，
  100 200 300 400 500 600-900 六种粗细
  其中 lighter 对应 100    normal 对应 400    bold 对应 700    bolder 对应 700

*  2 在 Chrome 下，默认字体粗细只有两种
   100-400 500-900 对应为 normal 和 bold，如果设置为 lighter，则等价于 normal， 设为 bolder 等价于 bold
   如果设为 "PingFang SC" 同样有六种字体粗细，参考上面
* 所以 font-weight 字体粗细跟字体有关。关于字体粗细的回退机制可以参考以下文章
